:root {
  --content-width: 1280px;

  @media screen and (max-width: 1439px) {
    --content-width: 980px;
    color: unset;
  }

  @media screen and (max-width: 1023px) {
    --content-width: 96%;
    color: unset; // -  , -             ,  -        
  }

  @media screen and (max-width: 767px) {
    --content-width: 90%;
  }
}